.App {
  max-width:860px;
  margin:40px auto;
}

button {
  background:none;
  border:2px solid #fff;
  padding:6px 12px;
  border-radius:4px;
  color:#fff;
  font-weight:bold;
  cursor:pointer;
  font-size:1em;
}

button:hover {
  background-color:#c23866;
  color:#fff;
}

.kortu-gridas {
  margin-top:40px;
  display:grid;
  grid-gap:20px;
  grid-template-columns:repeat(4, 1fr);
}

img {
  width:200px;
  height:200px;
}